.container {
  width: 100%;
  padding: 5vh 5vw;
  height: 100vh;
  background: #1b1919;
  color: #eee;
}

.headingContain {
  display: flex;
  justify-content: space-between;
}

.heading {
  font-family: "Nunito", sans-serif;
  font-size: 4vh;
}

.totalPrice {
  text-align: right;
  font-family: "Nunito", sans-serif;
  font-size: 4vh;
}

.row {
  display: flex;
  align-items: center;
  /* justify-content: space-between; */
  gap: 3vh 4.5vw;
  flex-wrap: wrap;
  flex-direction: row;
  margin-top: 5vh;
}

.card {
  width: 19vw;
  height: 42vh;
}

.imageContainer {
  width: 19vw;
  height: 35vh;
  display: flex;
  justify-content: center;
  align-items: center;
  border-radius: 1vw;
  background-color: #cd3333;
  overflow: hidden;
  position: relative;
}

.imageContainer::before {
  position: absolute;
  top: 50%;
  left: 50%;
  z-index: 2;
  display: block;
  content: "";
  width: 0;
  height: 0;
  background: rgba(255, 255, 255, 0.2);
  border-radius: 100%;
  -webkit-transform: translate(-50%, -50%);
  transform: translate(-50%, -50%);
  opacity: 0;
}

.imageContainer:hover::before {
  -webkit-animation: circle 0.75s;
  animation: circle 0.75s;
}

@-webkit-keyframes circle {
  0% {
    opacity: 1;
  }

  40% {
    opacity: 1;
  }

  100% {
    width: 200%;
    height: 200%;
    opacity: 0;
  }
}

@keyframes circle {
  0% {
    opacity: 1;
  }

  40% {
    opacity: 1;
  }

  100% {
    width: 200%;
    height: 200%;
    opacity: 0;
  }
}

.cardImage {
  width: 100%;
  height: 100%;
  transition: 0.3s ease-in-out;
}

.cardDetail {
  width: 100%;
  margin-top: 1vh;
  display: flex;
}

.cardLeft {
  width: 16vw;
}

.cardLink {
  text-decoration: none;
}

.cardHeading {
  font-size: 2vh;
  font-family: "Roboto Condensed", sans-serif;
  font-weight: 300;
  color: #eee;
  white-space: nowrap;
  width: 16vw;
  overflow: hidden;
  text-overflow: ellipsis;
}

.price {
  font-size: 2vh;
  font-family: "Roboto Condensed", sans-serif;
  font-weight: 500;
  color: #eee;
  margin-top: 0.2vh;
}

.cardRight {
  width: 3vw;
}

.iconContain {
  float: right;
}

.cartIcon {
  border: 0.1vw solid #171717;
  border-radius: 50%;
  padding: 0.7vh;
  font-size: 4vh;
  color: #171717;
  transition: 0.5s;
}

.cartIcon:hover {
  transform: scale(1.3) rotateX(360deg) rotateY(360deg) rotateZ(360deg);
  color: #ff7e22;
  border: 0.1vw solid #ff7e22;
}

.btnRow {
  margin-top: 3vh;
  display: flex;
  justify-content: center;
}

.btn {
  background-color: transparent;
  font-size: 2.3vh;
  font-family: "Raleway", sans-serif;
  color: #171717;
  border: 0.1vw solid #171717;
  padding: 1.5vh 2.5vw;
  border-radius: 0.7vw;
  transition: 0.3s ease-in;
}

.btn:hover {
  background-color: #171717;
  color: #f2f2f2;
  box-shadow: 0 0 5vh 1.25vh #171717;
  border: 0.1vw solid #f2f2f2;
}

.cartButton {
  background-color: transparent;
  border: none;
}

.btnContainer {
  display: flex;
  justify-content: space-between;
}

.clearCartBtn,
.buyBtn {
  margin-top: 5vh;
  font-size: 2vh;
  width: 13vw;
  height: 6vh;
  border-radius: 10px;
  border: 1.5px solid #171717;
  background-color: #fff;
  color: #171717;
  transition: 0.2s ease-in-out;
}

.clearCartBtn:hover,
.buyBtn:hover,
.buyLink:hover {
  background-color: #171717;
  color: #f2f2f2;
  border: 1.5px solid #f2f2f2;
  box-shadow: 0 0 5vh 1vh #171717;
}

.buyLink {
  text-decoration: none;
  color: #171717;
}
